import React from 'react';
import SearchBar from './Components/SearchBar';
import './App.css';
import GifList from './Components/GifList';

class App extends React.Component {

  state = { gif: [] }
  // onSearchSubmit = async (term, limit = 5,rating) => {
  //   const giphy = require('giphy-api-without-credentials')('dFWuvuX6BXqw2v1GVKmCVsLHPMDdxdvj');
  //   giphy.search({
  //     q:`${term}`,
  //     limit:limit,
  //     rating:rating
  //   }, (err, res) => {
  //     console.log(limit)
  //     console.log(res.data);
  //     this.setState({ gif: res.data });
  //   })
  // }
  onSearchSubmit = (term, limit = 5, rating) => {
    fetch(`https://api.giphy.com/v1/gifs/search?q=${term}&api_key=dFWuvuX6BXqw2v1GVKmCVsLHPMDdxdvj&limit=${limit}&rating=${rating}`, {
      method: 'GET',
      headers: {
        'Content-Type': 'application/json',
        'Accept': 'application/json'
      },
    }).then(r => r.json()).then(data => {
      this.setState({ gif: data.data });
    })
  }
  render() {
    return (
      <div>
        <SearchBar onSubmit={this.onSearchSubmit} />
        <GifList gifs={this.state.gif} />
      </div>
    );
  }
}

export default App;
